.header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0vh;
    left: 3vw;
    z-index: 1000;
    height: 18vh;
  
  
  }
  .appTitle {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  
  h1 {
    padding: 1vh 2vw;
    color: black;
    font-size: 3rem;
    text-shadow: 2px 2px pink;
  }
  
  .navLinks {
    margin-top: 3vh;
    width: 96vw;
    position: relative;
    height: 8vh;
  
  
  
  }
  
  .navLinks a {
    float: left;
  
  }
  
  .addBtn,
  .viewBtn {
      display: flex;
      justify-content: center;
      align-items: center;
    font-size: 1.5rem;
    height: 6vh;
    width: 47.5vw;
    text-decoration: none;
    text-align: center;
    background: $navlink;
    border-top-right-radius: 50px;
    letter-spacing: 1px;
    transition: all 280ms ease-in;
    text-decoration: none;
    outline: none;
    cursor: pointer;
    position: absolute;
   bottom: 0vh;
  
  }
  
  .addBtn {
    left: 0vw;
  }
  
  .viewBtn {
    left:47.5vw;
  }
  
  
  
  
  .activeButton {
    background: lighten(rgba(7, 116, 241, 0.9), 10%);
    letter-spacing: 1.5px;
    font-size: 2rem;
    transition: all 280ms ease-out;
    color: white;
    height: 8vh;
   
  }
  
  @media screen and (max-width: 600px) {
    h1 {
      font-size: 1.5rem;
      padding: 0;
    }
  
    a.is-active button,
    a button {
      font-size: 1.5rem;
      width: 20vw;
      height: 5vh;
      padding-bottom: 10px;
    }
  
    img {
      width: 50px;
      height: 30px;
    }
  }